      </div>
    </div>
    <div class="sidebar_child_menu">
      <div class="side_search">
        <input type="text" placeholder="Спорт тури ёки кодини киритинг">
        <svg width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg">
          <path d="M15.2583 14.075L12.425 11.25C13.3392 10.0854 13.8352 8.64722 13.8333 7.16667C13.8333 5.84813 13.4423 4.5592 12.7098 3.46287C11.9773 2.36654 10.9361 1.51206 9.71789 1.00747C8.49972 0.502889 7.15927 0.370866 5.86607 0.628101C4.57286 0.885336 3.38497 1.52027 2.45262 2.45262C1.52027 3.38497 0.885336 4.57286 0.628101 5.86607C0.370866 7.15927 0.502889 8.49972 1.00747 9.71789C1.51206 10.9361 2.36654 11.9773 3.46287 12.7098C4.5592 13.4423 5.84813 13.8333 7.16667 13.8333C8.64722 13.8352 10.0854 13.3392 11.25 12.425L14.075 15.2583C14.1525 15.3364 14.2446 15.3984 14.3462 15.4407C14.4477 15.4831 14.5567 15.5048 14.6667 15.5048C14.7767 15.5048 14.8856 15.4831 14.9872 15.4407C15.0887 15.3984 15.1809 15.3364 15.2583 15.2583C15.3364 15.1809 15.3984 15.0887 15.4407 14.9872C15.4831 14.8856 15.5048 14.7767 15.5048 14.6667C15.5048 14.5567 15.4831 14.4477 15.4407 14.3462C15.3984 14.2446 15.3364 14.1525 15.2583 14.075ZM2.16667 7.16667C2.16667 6.17776 2.45991 5.21106 3.00932 4.38882C3.55873 3.56657 4.33962 2.92571 5.25325 2.54727C6.16688 2.16883 7.17222 2.06982 8.14212 2.26274C9.11203 2.45567 10.0029 2.93187 10.7022 3.63114C11.4015 4.3304 11.8777 5.22131 12.0706 6.19122C12.2635 7.16112 12.1645 8.16646 11.7861 9.08009C11.4076 9.99372 10.7668 10.7746 9.94452 11.324C9.12228 11.8734 8.15558 12.1667 7.16667 12.1667C5.84059 12.1667 4.56882 11.6399 3.63114 10.7022C2.69345 9.76452 2.16667 8.49275 2.16667 7.16667Z" fill="#797979"/>
        </svg>
      </div>
      <div class="side_child_links" id="myGroup">
        <div v-for="(it,index) in sport_category" :key="index">
          <button   v-b-toggle="`collapse-${index}`" class="button_menus" @click="showCollapse(index)" >
            <div>
              {{it.unique_id}}. {{it.title}}
            </div>
            <svg  width="12" height="6" viewBox="0 0 12 6" fill="none" xmlns="http://www.w3.org/2000/svg">
              <path d="M11 1L9.25269 2.76297C7.85925 4.1689 7.16252 4.87187 6.31333 4.98011C6.10527 5.00663 5.89473 5.00663 5.68667 4.98011C4.83747 4.87187 4.14075 4.1689 2.74731 2.76297L1 1"  stroke-width="1.5" stroke-linecap="round"/>
            </svg>

          </button>
          <b-collapse :id="`collapse-${index}`" class="mt-2" data-bs-parent="#myGroup" accordion="my-accordion">
            <button  class="button_menus child_link"  :id="`statistic${ind}`" v-for="(item,ind) in it.sports" :key="ind" @click="getPrice(item.id,item.title,ind)">
              {{item.unique_id}}. {{item.title}}
            </button>
          </b-collapse>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default
{
  data(){
    return{
      isActive:false,
      sport_category:[]
    }
  },
  mounted() {
    if (localStorage.getItem('token') === null) {
      this.$router.push('/login')
    }
    this.$axiosDefault.get(`/sports-categories/`,
        {
          headers: {
            'Authorization': `Bearer ${localStorage.getItem('token')}`
          }
        })
        .then(res=>{
          this.sport_category=res.data
          this.$store.state.nav_title=this.sport_category[0].sports[0].title

          this.$axiosDefault.get(`/sports/${res.data[0].sports[0].id}/balances/`,
              {
                headers: {
                  'Authorization': `Bearer ${localStorage.getItem('token')}`
                }
              })
              .then(resp=>{
                this.$store.state.price=resp.data.results[0]

                this.$axiosDefault.get(`/sports/${res.data[0].sports[0].id}/balances/${resp.data.results[0].id}/`,
                    {
                      headers: {
                        'Authorization': `Bearer ${localStorage.getItem('token')}`
                      }
                    })
                    .then(response=>{
                      this.$store.state.balance=response.data

                      this.$axiosDefault.get(`/sports/${res.data[0].sports[0].id}/balances/${resp.data.results[0].id}/transactions/`,
                          {
                            headers: {
                              'Authorization': `Bearer ${localStorage.getItem('token')}`
                            }
                          })
                          .then(response=>{
                            this.$store.state.transactions=response.data
                            response.data.results.forEach(el=>{
                              this.$store.state.aj_mab=this.$store.state.aj_mab+Number(el.planned_amount)
                              this.$store.state.kassa=this.$store.state.kassa+Number(el.actual_amount)
                              this.$store.state.xis_top=this.$store.state.xis_top+Number(el.spent_amount)
                              this.$store.state.qay_mab=this.$store.state.qay_mab+Number(el.returned_amount)
                            })
                          })
                    })
              })
        })

  },
  methods:{
    showCollapse(index){
      this.isActive=!this.isActive

      let days = document.querySelectorAll('.button_menus')
      days.forEach(el=>el.classList.remove('not-collapsed'))
      var element = document.getElementById(`collapse-${index}`);
      element.classList.add("collapsed");
    },
    getPrice(item,name){
      // let days = document.querySelectorAll('.button_menus')
      // days.forEach(el=>el.classList.remove('active_m'))
      // var element = document.getElementById(`statistic${index}`);
      // element.classList.add("active_m");

      this.$store.state.nav_title=name
      this.$axiosDefault.get(`/sports/${item}/balances/`,
          {
            headers: {
              'Authorization': `Bearer ${localStorage.getItem('token')}`
            }
          })
          .then(res=>{
            this.$store.state.price=res.data.results[0]
            console.log(res.data.results.length)
            if(res.data.results.length==0){
              this.$store.state.transactions.results=[]
              this.$store.state.price={}
              this.$store.state.balance={}
            }
            this.$axiosDefault.get(`/sports/${item}/balances/${res.data.results[0].id}/`,
                {
                  headers: {
                    'Authorization': `Bearer ${localStorage.getItem('token')}`
                  }
                })
                .then(response=>{
                  console.log(response)
                  this.$store.state.balance=response.data
                  this.$axiosDefault.get(`/sports/${this.$store.state.price.sport}/balances/${this.$store.state.price.id}/transactions`,
                      {
                        headers: {
                          'Authorization': `Bearer ${localStorage.getItem('token')}`
                        }
                      })
                      .then(tran=>{
                        this.$store.state.qay_mab=0
                        this.$store.state.xis_top=0
                        this.$store.state.kassa=0
                        this.$store.state.aj_mab=0

                        this.$store.state.transactions=tran.data
                        tran.data.results.forEach(el=>{
                          this.$store.state.aj_mab=this.$store.state.aj_mab+Number(el.planned_amount)
                          this.$store.state.kassa=this.$store.state.kassa+Number(el.actual_amount)
                          this.$store.state.xis_top=this.$store.state.xis_top+Number(el.spent_amount)
                          this.$store.state.qay_mab=this.$store.state.qay_mab+Number(el.returned_amount)
                        })
                      })
                })
          })
    }
  }

}
</script>
